*{
    margin: 0;
}

*:focus{
    outline: none;
}


body::-webkit-scrollbar{
    width: 10px;
}

body::-webkit-scrollbar-track{
    background-color: #222222;
}

body::-webkit-scrollbar-thumb{
    background-color: #515151;
    border-radius: 1rem;
    scroll-behavior: smooth;
}